.todoListMain{
    margin-top: 100px;
    font-size: 16px;
}
.input{
    padding: 10px;
    font-size: 16px;
    border: 2px solid #FFF;
    width: 250px;
    margin:10px;
    border: 2px solid black;
    border-radius: 5px;
}
.submit{
    padding: 10px;
    font-size: 16px;
    border: 2px solid #FFF;
    width: 80px;
    background-color: chocolate;
    cursor: pointer;
    border: 2px solid black;
    border-radius: 5px;
}
.submit :hover{
    background-color: blueviolet;
}
.theList{
    list-style: none;

}
li{
    background-color: rgba(255,255,255,.5);
    margin-left: 38%;
    margin-right: 40%;
    padding: 10px;
    border-radius: 10px;
    margin-top:25px;
    margin-bottom: 25px;
    cursor: pointer;
}
li:hover{
    background-color: pink;
}